
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
name: "myAddress",
directives: {
TransferDom
},
components: {
ViewBox,
XHeader,
Card,
Loading,
Confirm
},
data() {
return {
addressList: [],
isSelect: false,
selectAddressId: '',
show: false,
addressId: ''
};
},
created () {
if (this.$route.name === "selectAddress") {
this.isSelect = true;
this.selectAddressId = this.$route.params.addressId;
} else {
this.isSelect = false;
}
this.queryUserAddress();
},
methods: {
toEditAddress(addressItem) {
this.$router.push({
name: 'editAddress',
params: { addressInfo: addressItem }
})
},
queryUserAddress() {
this.$vux.loading.show();
this.$api
.queryUserAddress({})
.then(data => {
if (data.code === 200) {
this.addressList = data.data.address;
}
this.$vux.loading.hide();
});
},
deleteUserAddress(id) {
this.$api
.deleteUserAddress({
id: id
})
.then(data => {
if (data.code === 200) {
this.$vux.toast.text("删除成功", "top");
this.$router.go(-1);
} else {
this.$vux.toast.text(data.message, "top");
}
});
},
toGetGoods(item) {
if(!this.isSelect) return;
this.$router.push({
name: 'getGoods',
params: {
addressId: item.id,
addressName: item.province + item.city + item.area + item.detail
}
})
},
showConfirm(id) {
this.show = true;
this.addressId = id;
},
onCancel() {

},
onConfirm() {
this.deleteUserAddress(this.addressId)
}
}
};



